        this.imgData[7] = res.fileinfo.fid
      },
      successImg9 (res, file) {
        this.imageUrl9 = URL.createObjectURL(file.raw);
        this.imgData[8] = res.fileinfo.fid
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === 'image/jpeg';
        const isLt5M = file.size / 1024 / 1024 < 5;

        if (!isJPG) {
          this.$message.error('上传头像图片只能是 JPG 格式!');
        }
        if (!isLt5M) {
          this.$message.error('上传头像图片大小不能超过 5MB!');
        }
        return isJPG && isLt5M;
      },
      isBrand (index) {
        let self = this
        self.brandShow = true
        document.title = '选择成色'
      },
      brandClick (cell) {
        let self = this
        self.brand = cell.name
        self.postData.brand_id = cell.id
        self.brandShow = false
      },
      conditionHide(index){
        let self = this
        if(index==1){
          self.conditionShow = false
          document.title = '爱表直卖'
        }else{
          self.conditionShow = true
          document.title = '选择成色'
        }
      },
      conditionClick(item){
        let self = this
        self.condition = item.name
        self.postData.fineness = item.id
        self.conditionShow = false
        document.title = '爱表直卖'
      },
      localClick(name){
        let self = this
        self.postData.location = name
        self.localShow = false
      },
      onAddressChange(picker, values) {
        let self= this
        picker.setSlotValues(1, address[values[0]]);
        self.localList[0] = values[0];
        self.localList[1] = values[1];
      },
      sureLocal(){
        let self= this
        self.postData.location = self.localList[0]+'/'+self.localList[1]
        self.local = self.localList[0]+'/'+self.localList[1]
        self.localShow = false
      },
      sub() {
        let self = this
        if(!self.postData.brand_id){
          self.$toast('请选择品牌')
          return false
        }else if (!self.postData.title) {
          self.$toast('请输入标题')
          return false
        }else if (!self.postData.details) {
          self.$toast('请输入描述')
          return false
        }else if(!self.imgData[0] || !self.imgData[1]||!self.imgData[2]||!self.imgData[3]||!self.imgData[4]){
          self.$toast('请上传手表的前5张图')
          return false
        }else if (!self.value[0]) {
          self.$toast('请勾选协议')
          return false
        }else if (!self.postData.fineness) {
          self.$toast('请选择成色')
          return false
        }else if (!self.postData.location) {
          self.$toast('请选择所在地')
          return false
        }else if (!self.postData.diameter) {
          self.$toast('请输入表盘直径')
          return false
        }else if (!self.postData.original_price) {
          self.$toast('请输入原价')
          return false
        }else if (!self.postData.price) {
          self.$toast('请输入售价')
          return false
        }else{
          self.postData.file_id = []
          for (let i=0;i<self.imgData.length;i++){
            if(self.imgData[i]){
              self.postData.file_id.push(self.imgData[i])
            }
          }
          self.$http.post(`${process.env.API.MARKET}/market/seller/publish`,self.postData).then(res=>{
            if(res.data.errcode=='0'){
              self.$router.push('/')
            }else{
              self.$messagebox.alert(res.data.msg)
            }
          }).catch(err=>{
            console.log(err)
          })
        }
      }
    },
    created (){
      let self = this
      let timeShow = setInterval(function () {
        document.title = '爱表发布流程'
        if (self.num <= 0) {
          clearInterval(timeShow)
          self.isProcess = false
          document.title = '爱表直卖'
        } else {
          self.num--
        }
      }, 1000)
    },
    mounted () {
      let self = this
      self.$nextTick(() => {
        setTimeout(() => {//这个是一个初始化默认值的一个技巧
          this.addressSlots[0].defaultIndex = 0;
        }, 100);
      });
//      self.$http.post('http://user.local.com/login',{tel:'13286844491',code:'000000'})
      self.$http.get(`${process.env.API.DICT}/dict/brand?uid=${self.$store.state.userId}`).then(res=>{
        if(res.data.errcode=='0') {
          self.brandList = res.data.data
        }
      }).catch(err=>{
        console.log(err)
      })
      self.$http.get(`${process.env.API.DICT}/dict/material?uid=${self.$store.state.userId}`).then(res=>{
        if(res.data.errcode=='0') {
          self.watchCase = res.data.data
        }
      }).catch(err=>{
        console.log(err)
      })
      self.$http.get(`${process.env.API.DICT}/dict/fineness?uid=${self.$store.state.userId}`).then(res=>{
        if(res.data.errcode=='0') {
          self.conditionList = res.data.data
        }
      }).catch(err=>{
        console.log(err)
      })
      self.$http.get(`${process.env.API.DICT}/dict/function?uid=${self.$store.state.userId}`).then(res=>{
        if(res.data.errcode=='0') {
          self.complex = res.data.data
        }
      }).catch(err=>{
        console.log(err)
      })
      self.$http.get(`${process.env.API.DICT}/dict/shape?uid=${self.$store.state.userId}`).then(res=>{
        if(res.data.errcode=='0') {
          self.shapeList = res.data.data
        }
      }).catch(err=>{
        console.log(err)
      })
    },
  }
</script>
