import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/pages/Index'
import Sell from '@/components/Sell'
import Bankcard from '@/components/Bankcard'
import Confirm from '@/components/Confirm'
import Banklist from '@/components/Banklist'
import Market from '@/components/Market'
import Search from '@/components/Search'
import Screen from '@/components/Screen'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/sell',
      name: 'sell',
      component: Sell
    },
    {
      path: '/bankcard',
      name: 'Bankcard',
      component: Bankcard
    },
    {
      path: '/confirm',
      name: 'Confirm',
      component: Confirm
    },
    {
      path: '/banklist',
      name: 'Banklist',
      component: Banklist
    },
    {
      path: '/market',
      name: 'Market',
      component: Market
    },
    {
      path: '/search',
      name: 'Search',
      component: Search
    },
    {
      path: '/screen',
      name: 'Screen',
      component: Screen
    }
  ]
})



