<template>
  <div class="search">
    <div class="tit">
      <div>
        <img src="../assets/images/market/search.png">
        <input type="text" placeholder="输入品牌或关键字">
      </div>
      <span @click="cancel">取消</span>
    </div>
    <div class="hot">
      <span>
        热门搜索
      </span>
      <ul>
        <li>百达翡丽</li>
        <li>百达翡丽</li>
        <li>百达翡丽</li>
        <li>百</li>
        <li>百达翡丽</li>
        <li>百达翡丽</li>
        <li>百达翡丽</li>
        <li>百达翡丽</li>
      </ul>
    </div>
    <div class="history">
      <span>历史搜索</span>
      <ul>
        <li>百达翡丽</li>
        <li>百达翡丽</li>
      </ul>
    </div>
  </div>
</template>
<script>
  export default{
    data(){
      return {

      }
    },
    methods:{
      cancel(){
        let self = this
        self.$router.push('/')
      }
    },
    mounted(){

    }
  }
</script>
<style lang="less" scoped>
  .search{
    height: 100vh;
    background: #fff;
    .tit{
      display: flex;
      justify-content: space-between;
      padding:10px 15px;
      div{
        display: flex;
        align-items: center;
        flex-wrap: nowrap;
        background: #ecf0f4;
        width: calc(~'100% - 40px');
        padding: 0 15px 0 10px;
        height: 24px;
        box-sizing: border-box;
        img{
          width: 15px;
          font-size: 15px;
          margin-right: 5px;
        }
        input{
          background: none;
          height: 100%;
          width: 100%;
        }
      }
      span{
        width: 32px;
        font-size: 16px;
        color: #333;
        display: inline-block;
      }
    }
    .hot{
      padding:15px 15px 0;
      border-top: 1px solid #f2f2f2;
      border-bottom: 1px solid #f2f2f2;
      span{
        font-size: 12px;
        color: #999;
        display: block;
        margin-bottom: 15px;
      }
      ul{
        display: flex;
        flex-wrap: wrap;
        li{
          background: #ecf0f4;
          color: #333;
          font-size: 12px;
          padding: 8px;
          margin-right: 15px;
          margin-bottom: 15px;
        }
      }
    }
    .history{
      padding: 15px 0 0 15px;
      span{
        font-size: 12px;
        color: #999;
        display: block;
        padding-bottom: 25px;
        border-bottom: 1px solid #f2f2f2;
      }
      ul{
        font-size: 14px;
        color: #333;
        li{
          display: flex;
          align-items: center;
          height: 44px;
          border-bottom: 1px solid #f2f2f2;
        }
      }
    }
  }
</style>
<style lang="less">
  body{
    padding: 0;
  }
</style>



