<template>
  <div class="bankList">
    <ul>
      <li v-for="(item,key) in bankList">
        <img src="../assets/images/market/sellProcess.png" alt="bankicon">
        <div>
          {{item.cardholder||'测试'}}
          <span>**** {{item.cardnum}}</span>
        </div>
      </li>
    </ul>
    <div class="foo" @click="relieve">
      解绑
    </div>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        bankList:[]
      }
    },
    methods:{
      relieve () {
        let self = this
        self.$messagebox.confirm('是否确定解除该银行卡？','提示').then(action=>{
          if(action == 'confirm'){
            self.$http.delete(`${process.env.API.USER}/user/bankcard?uid=${self.$store.state.userId}&id=${self.bankList[0].id}`).then(res=>{
              if(res.data.errcode=='0'){
               self.$router.push('/market')
              }
            }).catch(err=>{
              console.log(err)
            })
            self.$toast('解绑成功')
            setTimeout(()=>{
              self.$router.push('/')
            },1000)
          }
        });
      }
    },
    mounted(){
      let self = this
      document.title = '选择银行卡'
      self.$http.get(`${process.env.API.USER}/user/bankcard?uid=${self.$store.state.userId}`).then(res=>{
        if(res.data.errcode=='0'){
          self.bankList = res.data.data
          for (let i=0;i<self.bankList.length;i++){
            self.bankList[i].cardnum = res.data.data[i].cardnum.substring(0,4)
          }
        }
      }).catch(err=>{
        console.log(err)
      })
    }
  }
</script>
<style lang="less" scoped>
  .bankList{
    ul{
      margin-top: 10px;
      li{
        height: 75px;
        display: flex;
        align-items: center;
        padding: 0 15px;
        background: #fff;
        margin-bottom: 10px;
        div{
          font-size: 14px;
          line-height: 22px;
          display: flex;
          flex-direction: column;
        }
        img{
          width: 45px;
          height: 45px;
          border-radius: 50%;
          display: block;
          margin-right: 10px;
        }
      }
    }
    .foo{
      width: 100%;
      height: 44px;
      text-align: center;
      line-height: 44px;
      position: fixed;
      bottom: 0;
      left: 0;
      background: #333;
      color: #fff;
    }
  }
</style>
<style lang="less">
  body{
    padding: 0!important;
    background: #ecf0f4;
  }
</style>



