  <ul class="foo">
    <li @click="open(1)">
      <span class="foo1"></span>
      圈子
    </li>
    <li @click="open(2)">
      <span class="foo2"></span>
      资讯
    </li>
    <li @click="open(3)" class="fooActive">
      <span class="foo3 foo3Active"></span>
      挑表
    </li>
    <li @click="open(4)">
      <span class="foo4"></span>
      任意戴
    </li>
    <li @click="open(5)">
      <span class="foo5"></span>
      我的
    </li>
  </ul>
</template>
<script>
  export default {
    data() {
      return {
        fooNum: 0
      }
    },
    methods: {
      open(index) {
        switch (index) {
          case 1:
            location.href = ''
            break;
          case 2:
            location.href = ''
            break;
          case 3:
            location.href = ''
            break;
          case 4:
            location.href = ''
            break;
          case 5:
//            location.href = ''
            this.$router.push('/sell')
            break;
        }
      }
    }
  }
</script>
<style lang="less" scoped>
  .foo {
    position: fixed;
    z-index: 1999;
    bottom: 0;
    left: 0;
    width: 100%;
    display: flex;
    flex-wrap: nowrap;
    border-top: 1px solid #f2f2f2;
    background: #fff;
    li {
      display: flex;
      flex-direction: column;
      align-items: center;
      height: 49px;
      justify-content: center;
      width: 20%;
      box-sizing: border-box;
      font-size: 12px;
      color: #999;
      &.fooActive {
        color: #333;
      }
      span {
        width: 34px;
        height: 21px;
        margin: 0 2px 2px 0;
        background-size: cover;
        background-repeat: no-repeat;
        &.foo1 {
          background-image: url('../assets/images/toolbar/friends.png');
        }
        &.foo2 {
          background-image: url('../assets/images/toolbar/news.png');
        }
        &.foo3 {
          background-image: url('../assets/images/toolbar/watch.png');
        }
        &.foo4 {
          background-image: url('../assets/images/toolbar/arbitrarily.png');
        }
        &.foo5 {
          background-image: url('../assets/images/toolbar/my.png');
        }
        &.foo1Active {
          background-image: url('../assets/images/toolbar/friendsActive.png');
        }
        &.foo2Active {
          background-image: url('../assets/images/toolbar/newsActive.png');
        }
        &.foo3Active {
          background-image: url('../assets/images/toolbar/watchActive.png');
        }
        &.foo4Active {
          background-image: url('../assets/images/toolbar/arbitrarilyActive.png');
        }
        &.foo5Active {
          background-image: url('../assets/images/toolbar/myActive.png');
        }
      }
      img {
        width: 34px;
        margin-right: 1px;
        margin-bottom: 2px;
      }
    }
    &.foo2 {
      background-image: url('../assets/images/toolbar/news.png');
    }
    &.foo3 {
      background-image: url('../assets/images/toolbar/watch.png');
    }
    &.foo4 {
      background-image: url('../assets/images/toolbar/arbitrarily.png');
    }
    &.foo5 {
      background-image: url('../assets/images/toolbar/my.png');
    }
    &.foo1Active {
      background-image: url('../assets/images/toolbar/friendsActive.png');
    }
    &.foo2Active {
      background-image: url('../assets/images/toolbar/newsActive.png');
    }
    &.foo3Active {
      background-image: url('../assets/images/toolbar/watchActive.png');
    }
    &.foo4Active {
      background-image: url('../assets/images/toolbar/arbitrarilyActive.png');
    }
    &.foo5Active {
      background-image: url('../assets/images/toolbar/myActive.png');
    }
  }

  img {
    width: 34px;
    margin-right: 1px;
    margin-bottom: 2px;
  }
</style>



