<script>
  import toolbar from '../components/toolbar'

  export default {
    data() {
      return {
        navNum: 1,
        screenShow: false,
        postData: {},
        //筛选
        brand: this.$t('message.brand'),
        shapeNum: null,
        sexNum: null,
        moneyNum: null,
        movementNum: null,
        watchCaseNum: null,
        isProcess: true,
        number: null,
        brandShow: false,
        conditionShow: false,
        diameterNum: null,
        bankList:[],
        diameterList: [
          '36mm以下',
          '36-38mm',
          '39mm',
          '42-45mm'
        ],
        condition: '请选择',
        gender: ['男士', '女士', '中性'],
        brandList: [],
        conditionList: [],
        shapeList: [],
        watchCase: [],
        complex: [],
        movementList: [
          '机芯',
          '鸭心',
          '鹅心'
        ],
        postScreen: {
          diameter: '',
          brand_id: '',
          money1: '',
          money2: '',
          material_id: '',
          shape_id: '',
          details: '',
          function_id: [],
          fineness: ''
        }
      }
    },
    methods: {
      openSearch() {
        this.$router.push('/search')
      },
      navClick(index) {
        let self = this
        self.navNum = index
        switch (index) {
          case 1: {
            console.log(index)
            break;
          }
          case 2: {
            console.log(index)
            break;
          }
          case 3: {
            console.log(index)
            break;
          }
          case 4: {
            self.screenShow = true
            break;
          }
        }
      },
      isBrand(index) {
        let self = this
        self.brandShow = true
        document.title = '选择成色'
      },
      moneyClick(index) {
        let self = this
        if (self.moneyNum == index) {
          self.postData.money1 = ''
          self.postData.money2 = ''
          self.moneyNum = null
          return false
        }
        switch (index) {
          case 1:
            self.postData.money1 = 0
            self.postData.money2 = 10
            setTimeout(() => {
              self.moneyNum = index
            })
            break;
          case 2:
            self.postData.money1 = 10
            self.postData.money2 = 50
            setTimeout(() => {
              self.moneyNum = index
            })
            break;
          case 3:
            self.postData.money1 = 50
            self.postData.money2 = 200
            setTimeout(() => {
              self.moneyNum = index
            })
            break
        }
      },
      genderClick(index) {
        let self = this
        if (index == self.sexNum) {
          self.sexNum = null
          self.sex = ''
        } else {
          self.sexNum = index
          self.postData.sex = index + 1
        }
      },
      brandClick(cell) {
        let self = this
        self.brand = cell.name
        self.postData.brand_id = cell.id
        self.brandShow = false
      },
      conditionHide(index) {
        let self = this
        if (index == 1) {
          self.conditionShow = false
          document.title = '爱表直卖'
        } else {
          self.conditionShow = true
          document.title = '选择成色'
        }
      },
      conditionClick(item) {
        let self = this
        self.condition = item.name
        self.postData.fineness = item.id
        self.conditionShow = false
        document.title = '爱表直卖'
      },
      watchCaseClick(item, index) {
        let self = this
        if (index == self.watchCaseNum) {
          self.watchCaseNum = null
          self.postData.material_id = ''
        } else {
          self.watchCaseNum = index
          self.postData.material_id = item.id
        }
      },
      complexClick(item, index) {
        let self = this
        if (!self.complex[index].wrap) {
          self.complex[index].wrap = true
          self.postData.function_id.push(item.manage.id)
        } else {
          self.complex[index].wrap = false
          for (let i = 0; i < self.complex.length; i++) {
            if (self.postData.function_id[i] == item.manage.id) {
              self.postData.function_id.splice(i, 1)
            }
          }
        }
      },
      shapeClick(item, index) {
        let self = this
        if (self.shapeNum == index) {
          self.shapeNum = null
          self.postData.shape_id = ''
        } else {
          self.shapeNum = index
          self.postData.shape_id = item.id
        }
      },
      movementClick(item, index) {
        let self = this
        if (self.movementNum == index) {
          self.movementNum = null
        } else {
          self.movementNum = index
        }
      },
      diameterClick(item, index) {
        let self = this
        if (self.diameterNum == index) {
          self.diameterNum = null
        } else {
          self.diameterNum = index
        }
      },
      sub(index) {
        let self = this
        if (index != 1) {
          console.log(self.postData)
        } else {
//          self.$router.push('/')
          self.screenShow = false
        }
      },
      demo(index){
        let self = this
        if(index==1){
          if (!self.bankList.length) {
            self.$router.push('/bankcard')
          }else{
            self.$router.push('/banklist')
          }
        }else{
          if (!self.bankList.length) {
            self.$messagebox.confirm('',{
              message:'银行卡用于您卖表的收款、退款',
              title:'发布前，请您先绑定银行卡！',
              confirmButtonText:'前往绑定',
              cancelButtonText:'暂不绑定'
            }).then(action=>{
              if(action == 'confirm'){
                self.$router.push('/bankcard')
              }
            })
          }else{
            self.$router.push('/sell')
          }
        }
//        if (!self.bankList.length) {
//          self.$messagebox.confirm('',{
//            message:'银行卡用于您卖表的收款、退款',
//            title:'发布前，请您先绑定银行卡！',
//            confirmButtonText:'前往绑定',
//            cancelButtonText:'暂不绑定'
//          }).then(action=>{
//            if(action == 'confirm'){
//              self.$router.push('/bankcard')
//            }
//          })
//        }else{
//          self.$router.push('/sell')
//        }
      }
    },
    mounted() {
      function GetQueryString(name) {
        var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)");
        var r = window.location.hash.replace('#/?', '').match(reg);
        if (r != null) return unescape(r[2]);
        return null;
      }

      // 调用方法
      if (GetQueryString("uid")) {
        localStorage.setItem('userId', GetQueryString("uid"))
        this.$store.dispatch('user', {uid: localStorage.getItem('userId')})
      }

      let self = this
      let uid = self.$store.state.userId
      self.$http.get(`${process.env.API.DICT}/dict/brand?uid=${uid}`).then(res => {
        if(res.data.errcode=='0'){
          self.brandList = res.data.data
        }
      }).catch(err => {
        console.log(err)
      })
      self.$http.get(`${process.env.API.DICT}/dict/material?uid=${uid}`).then(res => {
        if(res.data.errcode=='0'){
          self.watchCase = res.data.data
        }
      }).catch(err => {
        console.log(err)
      })
      self.$http.get(`${process.env.API.DICT}/dict/fineness?uid=${uid}`).then(res => {
        if(res.data.errcode=='0') {
          self.conditionList = res.data.data
        }
      }).catch(err => {
        console.log(err)
      })
      self.$http.get(`${process.env.API.DICT}/dict/function?uid=${uid}`).then(res => {
        if(res.data.errcode=='0') {
          self.complex = res.data.data
        }
      }).catch(err => {
        console.log(err)
      })
      self.$http.get(`${process.env.API.DICT}/dict/shape?uid=${uid}`).then(res => {
        if(res.data.errcode=='0') {
          self.shapeList = res.data.data
        }
      }).catch(err => {
        console.log(err)
      })
      self.$http.get(`${process.env.API.USER}/user/bankcard?uid=${uid}`).then(res => {
        if(res.data.errcode=='0') {
          self.bankList = res.data.data
        }
      }).catch(err => {
        console.log(err)
      })
    },
    watch: {
      'postData.money1'() {
        let self = this
        if ((self.postData.money1 >= 0 && self.postData.money1 != '' && self.postData.money1 <= 10) && (self.postData.money2 <= 10 && self.postData.money2 != '')) {
          self.moneyNum = 1
        } else if ((self.postData.money1 >= 10 && self.postData.money1 != '' && self.postData.money1 <= 50) && (self.postData.money2 <= 50 && self.postData.money2 != '')) {
          self.moneyNum = 2
        } else if ((self.postData.money1 >= 50 && self.postData.money1 != '' && self.postData.money1 <= 200) && (self.postData.money2 <= 200 && self.postData.money2 != '')) {
          self.moneyNum = 3
        } else {
          self.moneyNum = null
          return false
        }
      },
      'postData.money2'() {
        let self = this
        if ((self.postData.money1 >= 0 && self.postData.money1 != '' && self.postData.money1 <= 10) && (self.postData.money2 <= 10 && self.postData.money2 != '')) {
          self.moneyNum = 1
        } else if ((self.postData.money1 >= 10 && self.postData.money1 != '') && (self.postData.money2 <= 50 && self.postData.money2 != '')) {
          self.moneyNum = 2
        } else if ((self.postData.money1 >= 50 && self.postData.money1 != '' && self.postData.money1 <= 200) && (self.postData.money2 <= 200 && self.postData.money2 != '')) {
          self.moneyNum = 3
        } else {
          self.moneyNum = null
          return false
        }
      }
    },
    components: {toolbar}//公共底部
  }
</script>
