      }
    }
    .btn {
      position: fixed;
      width: 100%;
      height: 44px;
      border-top: 1px solid #f2f2f2;
      bottom: 0;
      left: 0;
      display: flex;
      align-items: center;
      span {
        display: inline-block;
        width: 50%;
        text-align: center;
        height: 44px;
        line-height: 44px;
        background: #fff;
        z-index: 999;
        &:last-child {
          background: #333;
          color: #fff;
        }
      }
    }
  }
</style>
<!--公共less样式-->
<style lang="less">
  body {
    padding: 0 !important;
  }

  .brandStyle {
    display: flex !important;
    align-items: center;
    span {
      color: #333;
    }
    .mint-cell-title {
      flex: 0;
    }
    img {
      width: 60px;
      height: 30px;
      margin-right: 10px;
    }
  }
</style>



