<template>
  <div class="screen">
    <div class="selStyle"  @click="isBrand">
      <div>品牌<span>（必选）</span></div>
      <div :class="{'fooColor':brand=='请选择一个品牌'}">{{brand}} <img src="../assets/images/market/arrow.png"></div>
    </div>
    <div class="selStyle"  @click="conditionHide">
      <div>成色<span>（必选）</span></div>
      <div :class="{'fooColor':condition=='请选择'}">{{condition}} <img src="../assets/images/market/arrow.png"></div>
    </div>
    <div class="money">
      <p class="moneyTie">价格区间（万元）</p>
      <div><input type="number" v-model="postData.money1"><p></p><input type="number" v-model="postData.money2"></div>
      <ul>
        <li><div @click="moneyClick(1)" :class="{'active':moneyNum == 1}">0-10</div></li>
        <li><div @click="moneyClick(2)" :class="{'active':moneyNum == 2}">10-50</div></li>
        <li><div @click="moneyClick(3)" :class="{'active':moneyNum == 3}">50-200</div></li>
      </ul>
    </div>
    <div class="wrap">
      <p>机芯类型</p>
      <ul>
        <li v-for="item,index in movementList">
          <div :class="{'active':movementNum==index}" @click="movementClick(item,index)">
            {{item}}
          </div>
        </li>
      </ul>
    </div>
    <div class="wrap">
      <p>性别</p>
      <ul>
        <li v-for="item,index in gender">
          <div :class="{'active':sexNum == index}" @click="genderClick(index)">
            {{item}}
          </div>
        </li>
      </ul>
    </div>
    <div class="wrap">
      <p>表壳材质</p>
      <ul>
        <li v-for="item,index in watchCase">
          <div :class="{'active':watchCaseNum==index}" @click="watchCaseClick(item,index)">
            {{item.name}}
          </div>
        </li>
      </ul>
    </div>
    <div class="wrap">
      <p>表壳形状</p>
      <ul>
        <li v-for="item,index in shapeList">
          <div :class="{'active':shapeNum==index}" @click="shapeClick(item,index)">
            {{item.name}}
          </div>
        </li>
      </ul>
    </div>
    <div class="wrap">
      <p>表盘直径</p>
      <ul>
        <li v-for="item,index in diameterList">
          <div :class="{'active':diameterNum==index}" @click="diameterClick(item,index)">
            {{item}}
          </div>
        </li>
      </ul>
    </div>
    <div class="wrap">
      <p>复杂功能</p>
      <ul>
        <li v-for="item,index in complex">
          <div :class="{'active':item.wrap}" @click="complexClick(item,index)">
            {{item.manage.name}}
          </div>
        </li>
      </ul>
    </div>
    <div style="width: 100%;height: 50px"></div>
    <div class="btn">
      <span @click="sub(1)">取消</span>
      <span @click="sub(2)">确认</span>
    </div>
    <!--品牌弹窗显示-->
    <mt-popup v-model="brandShow" position="bottom" class="mint-popup-bottom" :modal="false">
      <div class="close">
        <img src="../assets/images/market/close.png"  @click="brandShow = false">
      </div>
      <div style="height: 44px;width: 100%"></div>
      <mt-index-list>
        <mt-index-section  v-for="item,index in brandList" :index="item.initial" :key="index">
          <mt-cell v-for="cell,num in item.list" @click.native="brandClick(cell)" :key="num" class="brandStyle">
            <img :src="cell.pic" alt=""><span>{{cell.name}}</span>
          </mt-cell>
        </mt-index-section>
      </mt-index-list>
    </mt-popup>
    <!--成色弹出显示-->
    <mt-popup v-model="conditionShow" position="bottom" class="mint-popup-bottom" :modal="false">
      <div class="close">
        <img src="../assets/images/market/close.png"  @click="conditionHide(1)">
      </div>
      <div style="height: 44px;width: 100%"></div>
      <ul class="condition">
        <li v-for="item in conditionList" @click="conditionClick(item)">
          【{{item.name}}】<span>{{item.details}}</span>
        </li>
      </ul>
    </mt-popup>
  </div>
</template>
<script>
  export default{
    data(){
      return {
        brand:this.$t('message.brand'),
        shapeNum:null,
        sexNum:null,
        moneyNum:null,
        movementNum:null,
        watchCaseNum:null,
        isProcess:true,
        number:null,
        brandShow:false,
        conditionShow:false,
        diameterNum:null,
        diameterList:[
          '36mm以下',
          '36-38mm',
          '39mm',
          '42-45mm'
        ],
        condition:'请选择',
        gender:['男士','女士','中性'],
        brandList: [],
        conditionList:[],
        shapeList:[],
        watchCase:[],
        complex:[],
        movementList:[
          '机芯',
          '鸭心',
          '鹅心'
        ],
        postData:{
          diameter:'',
          brand_id:'',
          money1:'',
          money2:'',
          material_id:'',
          shape_id:'',
          details:'',
          function_id:[],
          fineness:''
        }
      }
    },
    methods:{
      isBrand (index) {
        let self = this
        self.brandShow = true
        document.title = '选择成色'
      },
      moneyClick(index){
        let self = this
        if(self.moneyNum==index){
          self.postData.money1=''
          self.postData.money2=''
          self.moneyNum = null
          return false
        }
        switch (index){
          case 1:
            self.postData.money1=0
            self.postData.money2=10
            setTimeout(()=>{
              self.moneyNum = index
            })
            break;
          case 2:
            self.postData.money1=10
            self.postData.money2=50
            setTimeout(()=>{
              self.moneyNum = index
            })
            break;
          case 3:
            self.postData.money1=50
            self.postData.money2=200
            setTimeout(()=>{
              self.moneyNum = index
            })
            break
        }
      },
      genderClick (index) {
        let self = this
        if(index==self.sexNum){
          self.sexNum = null
          self.sex = ''
        }else{
          self.sexNum = index
          self.postData.sex = index+1
        }
      },
      brandClick (cell) {
        let self = this
        self.brand = cell.name
        self.postData.brand_id = cell.id
        self.brandShow = false
      },
      conditionHide(index){
        let self = this
        if(index==1){
          self.conditionShow = false
          document.title = '爱表直卖'
        }else{
          self.conditionShow = true
          document.title = '选择成色'
        }
      },
      conditionClick(item){
        let self = this
        self.condition = item.name
        self.postData.fineness = item.id
        self.conditionShow = false
        document.title = '爱表直卖'
      },
      watchCaseClick (item,index) {
        let self = this
        if(index==self.watchCaseNum){
          self.watchCaseNum = null
          self.postData.material_id = ''
        }else{
          self.watchCaseNum = index
          self.postData.material_id = item.id
        }
      },
      complexClick (item,index){
        let self = this
        if(!self.complex[index].wrap){
          self.complex[index].wrap = true
          self.postData.function_id.push(item.manage.id)
        }else{
          self.complex[index].wrap = false
          for (let i=0;i<self.complex.length;i++){
            if(self.postData.function_id[i]==item.manage.id){
              self.postData.function_id.splice(i,1)
            }
          }
        }
      },
      shapeClick (item,index) {
        let self = this
        if(self.shapeNum == index){
          self.shapeNum = null
          self.postData.shape_id = ''
        }else{
          self.shapeNum = index
          self.postData.shape_id = item.id
        }
      },
      movementClick(item,index){
        let self = this
        if( self.movementNum == index){
          self.movementNum = null
        }else{
          self.movementNum = index
        }
      },
      diameterClick(item,index){
        let self = this
        if( self.diameterNum == index){
          self.diameterNum = null
        }else{
          self.diameterNum = index
        }
      },
      sub(index){
        let self = this
        if (index!=1){
          console.log(self.postData)
        }else{
          self.$router.push('/')
        }
      }
    },
    mounted(){
      let self = this
      self.$http.get('http://dict.local.com/dict/brand').then(res=>{
        self.brandList = res.data.data
      }).catch(err=>{
        console.log(err)
      })
      self.$http.get('http://dict.local.com/dict/material').then(res=>{
        self.watchCase = res.data.data
      }).catch(err=>{
        console.log(err)
      })
      self.$http.get('http://dict.local.com/dict/fineness').then(res=>{
        self.conditionList = res.data.data
      }).catch(err=>{
        console.log(err)
      })
      self.$http.get('http://dict.local.com/dict/function').then(res=>{
        self.complex = res.data.data
      }).catch(err=>{
        console.log(err)
      })
      self.$http.get('http://dict.local.com/dict/shape').then(res=>{
        self.shapeList = res.data.data
      }).catch(err=>{
        console.log(err)
      })
    },
    watch:{
      'postData.money1'(){
        let self = this
        if((self.postData.money1>=0&&self.postData.money1!=''&&self.postData.money1<=10)&&(self.postData.money2<=10&&self.postData.money2!='')){
          self.moneyNum = 1
        }else if((self.postData.money1>=10&&self.postData.money1!=''&&self.postData.money1<=50)&&(self.postData.money2<=50&&self.postData.money2!='')){
          self.moneyNum = 2
        }else if((self.postData.money1>=50&&self.postData.money1!=''&&self.postData.money1<=200)&&(self.postData.money2<=200&&self.postData.money2!='')){
          self.moneyNum = 3
        }else{
          self.moneyNum = null
          return false
        }
      },
      'postData.money2'(){
        let self = this
        if((self.postData.money1>=0&&self.postData.money1!=''&&self.postData.money1<=10)&&(self.postData.money2<=10&&self.postData.money2!='')){
          self.moneyNum = 1
        }else if((self.postData.money1>=10&&self.postData.money1!='')&&(self.postData.money2<=50&&self.postData.money2!='')){
          self.moneyNum = 2
        }else if((self.postData.money1>=50&&self.postData.money1!=''&&self.postData.money1<=200)&&(self.postData.money2<=200&&self.postData.money2!='')){
          self.moneyNum = 3
        }else{
          self.moneyNum = null
          return false
        }
      }
    }
  }
</script>
<style lang="less" scoped>
  .screen{
    padding: 0 0 15px 15px;
    background: #fff;
    margin-bottom: 30px;
    .money{
      border-bottom: 1px solid #f2f2f2;
      .moneyTie{
        font-size: 12px;
        color: #999;
        padding: 15px 0 10px;
      }
      div{
        display: flex;
        align-items: center;
        margin-bottom: 15px;
        p{
          width: 66px;
          display: inline-block;
          height: 1px;
          background: #999;
        }
        input{
          background: #ecf0f4;
          width: 100px;
          min-height: 32px;
          text-align: center;
          &:first-child{
            margin-right: 7.5px!important;
          }
          &:last-child{
            margin-left: 7.5px!important;
          }
        }

      }
      ul{
        display: flex;
        li{
          width: 25%;
          display: flex;
          align-items: center;
          div{
            width: 80%;
            height: 24px;
            background: #ecf0f4;
            text-align: center;
            font-size: 12px;
            color: #333;
            display: flex;
            justify-content: center;
            &.active{
              background: #333;
              color: #fff;
            }
          }
        }
      }
    }
    .wrap{
      border-bottom: 1px solid #f2f2f2;
      padding-bottom: 15px;
      p{
        padding: 15px 0;
      }
      ul{
        display: flex;
        flex-wrap: wrap;
        li{
          width: 25%;
          display: flex;
          margin-bottom: 10px;
          box-sizing: border-box;
          div{
            width: 80px;
            height: 24px;
            background: #ecf0f4;
            box-sizing: border-box;
            color: #333;
            text-align: center;
            font-size: 12px;
            line-height: 24px;
            &.active{
              background: #333;
              color: #fff;
            }
          }
        }
      }
    }
    .selStyle{
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 44px;
      border-bottom: 1px solid #f2f2f2;
      input{
        border: none;
        text-align: right;
        padding-right: 10px;
        box-sizing: border-box;
      }
      div{
        color: #333;
        font-size: 14px;
        padding-right: 15px;
        span{
          color: #999;
          font-size: 12px;
          &.rmb{
            font-size: 14px;
            color: #333;
          }
        }
        img{
          width: 7px;
          height: 12px;
        }
      }
    }
    .fooColor{
      color: #ccc!important;
    }
    .condition {
      padding: 0 15px;
      li {
        color: #333;
        font-size: 14px;
        display: flex;
        min-height: 44px;
        padding: 10px 0;
        border-top: 1px solid #f2f2f2;
        box-sizing: border-box;
        &:last-child {
          border-bottom: 1px solid #f2f2f2;
        }
        span {
          display: inline-block;
          padding-top: 2px;
          width: 80%;
          font-size: 12px;
          color: #666;
          padding-left: 10px;
        }
      }
    }
    .btn{
      position: fixed;
      width: 100%;
      height: 44px;
      border-top: 1px solid #f2f2f2;
      bottom: 0;
      left: 0;
      display: flex;
      align-items: center;
      span{
        display: inline-block;
        width: 50%;
        text-align: center;
        height: 44px;
        line-height: 44px;
        background: #fff;
        z-index: 999;
        &:last-child{
          background: #333;
          color: #fff;
        }
      }
    }
  }
</style>
<style lang="less">
  body{
    padding: 0;
  }
  .brandStyle{
    display: flex!important;
    align-items: center;
    span{
      color: #333;
    }
    .mint-cell-title{
      flex:0;
    }
    img{
      width: 60px;
      height: 30px;
      margin-right: 10px;
    }
  }
</style>



