<template>
  <div class="bankcard">
    <div class="addBankCard">
      <p class="tit">
        请绑定持卡人本人的银行卡（储蓄卡）
      </p>
      <ul>
        <li>持卡人：<input type="text" v-model="postData.cardholder"></li>
        <li>卡号：<input type="number" v-model="postData.cardnum"></li>
      </ul>
      <div class="bank">
        所属银行：
        <p><img :src="bankList.length>=1?bankList[bankNum].pic:''" alt="">{{bank}}</p>
        <select v-model="postData.bank_id" @change="selectVal">
          <option v-for="item,index in bankList" :value="item.id">{{item.name}}</option>
        </select>
      </div>
      <div class="btn"  @click="nextClick">
        下一步
      </div>
      <div class="foo">
        提示：请绑定持卡人本人的银行卡（储蓄卡），只能绑定一张银行卡，用于您卖表的收款，退款的银行账号
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    data (){
      return {
        bankList:[],
        bank:'',
        bankNum:0,
        postData:{
          cardholder:'',
          cardnum:'',
          bank_id:''
        }
      }
    },
    methods: {
      selectVal(ele){
        let self = this
        for (let i=0;i<self.bankList.length;i++){
          if(self.bankList[i].id == self.postData.bank_id){
            self.bank = self.bankList[i].name
            self.bankNum = i
          }
        }
      },
      nextClick() {
        let self = this
        if (!self.postData.cardholder){
          self.$toast('请输入持卡人')
        }else if (!self.postData.cardnum){
          self.$toast('请输入卡号')
        }else{
          localStorage.setItem('bankInfo',JSON.stringify(self.postData))
          self.$router.push('/confirm')
        }
      }
    },
    created(){
      document.title = '添加银行卡'
      let self = this
      let bankInfo = JSON.parse(localStorage.getItem('bankInfo'))
      self.$http.get(`${process.env.API.DICT}/dict/bank`).then(res=>{
        if(res.data.errcode=='0'){
          self.bankList = res.data.data
          if(!bankInfo){
            self.bankNum = 0
            self.bank= self.bankList[self.bankNum].name
            self.postData.bank_id= self.bankList[self.bankNum].id
          }else{
            self.postData.cardholder = bankInfo.cardholder
            self.postData.cardnum = bankInfo.cardnum
            self.postData.bank_id = bankInfo.bank_id
          }
          for (let i=0;i<self.bankList.length;i++){
            if(self.bankList[i].id == self.postData.bank_id){
              self.bank = self.bankList[i].name
              self.bankNum = i
            }
          }
        }
      }).catch(err=>{
        console.log(err)
      })
    },
    mounted(){

    }
  }
</script>
<style lang="less" scoped>
  .bankcard{
    .addBankCard{
      .tit{
        font-size: 12px;
        color: #666;
        padding: 15px;
      }
      .bank{
        p{
          display: flex;
          align-items: center;
          justify-content: flex-end;
          img{
            width: 20px;
            height: 20px;
            border-radius: 50%;
            margin-right: 5px;
          }
        }
      }
      ul{
        background: #fff;
        padding: 0 15px;
        font-size: 14px;
        li{
          height: 44px;
          color: #333;
          display: flex;
          align-items: center;
          justify-content: space-between;
          input{
            width: 70%;
            height: 30px;
            text-align: right;
          }
          &:first-child{
            border-bottom: 1px solid #f2f2f2;
          }
        }
      }
      div{
        height: 44px;
        background: #fff;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 15px;
        margin-top: 10px;
        position: relative;
        font-size: 14px;
        p{
          color: #333;
          height: 44px;
          padding: 0;
          min-width: 40%;
          position: absolute;
          right: 15px;
          top:0;
          text-align: right;
          line-height: 44px;
        }
        select{
          opacity: 0;
        }
      }
      .btn{
        width: 250px;
        height: 44px;
        background: #333;
        color: #fff;
        margin: auto;
        text-align: center;
        line-height: 44px;
        display: block;
        margin-top: 75px;
        font-size: 16px;
      }
      .foo{
        color: #999;
        font-size: 12px;
        width: 300px;
        background: #ecf0f4;
        margin: auto;
        line-height: 22px;
        margin-top: 30px;
      }
    }
  }
</style>
<style lang="less">
  //移除body
  body{
    padding: 0!important;
    background: #ecf0f4!important;
  }
</style>



