<template>
  <div class="confirm">
    <ul>
      <li>持卡人<span>{{postData.cardholder}}</span></li>
      <li>卡号<span>{{postData.cardnum}}</span></li>
      <li>所属银行<span><img :src="bankList.length>=1?bankList[bankNum].pic:''">{{bank}}</span></li>
    </ul>
    <div class="btn" @click="add">
      确认添加
    </div>
    <div class="foo">
      提示：用于提示您买表的收款、退款的银行账号，请务必仔细核对银行卡信息后进行添加
    </div>
  </div>
</template>
<script>
  export default {
    data (){
      return {
        bank:'',
        bankList:[],
        bankNum:0,
        postData:{
          cardholder:'',
          cardnum:'',
          bank_id:'',
          uid:this.$store.state.userId
        }
      }
    },
    methods:{
      add () {
        let self = this
        self.$http.post('http://user.local.com/user/bankcard', self.postData).then(res => {
          if (res.data.errcode=='0') {
            self.$toast({
              message: '操作成功',
              className:'bg'
            });
            setTimeout(()=>{
              localStorage.removeItem('bankInfo')
              self.$router.push('/banklist')
            },3000)
          }else{
            self.$toast('操作失败')
          }
        }).catch(err => {
          console.log(err)
        })
      }
    },
    mounted(){
      let self = this
      document.title = '银行卡信息'
      let bankInfo = JSON.parse(localStorage.getItem('bankInfo'))
      if (!bankInfo) {
        self.$router.push('/')
        return false
      } else {
        self.$http.get(`${process.env.API.DICT}/dict/bank`).then(res=> {
          if (res.data.errcode=='0') {
            self.bankList = res.data.data
            self.postData.cardholder = bankInfo.cardholder
            self.postData.cardnum = bankInfo.cardnum
            self.postData.bank_id = bankInfo.bank_id
            for (let i = 0; i < self.bankList.length; i++) {
              if (self.bankList[i].id == self.postData.bank_id) {
                self.bank = self.bankList[i].name
                self.bankNum = i
              }
            }
          }
        }).catch(err=>{
          console.log(err)
        })
      }
    }
  }
</script>
<style lang="less" scoped>
  .confirm{
    ul{
      margin-top: 10px;
      background: #fff;
      li{
        color: #999;
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 44px;
        padding: 0 15px;
        font-size: 14px;
        &:last-child{
          img{
            width: 20px;
            height: 20px;
            border-radius: 50%;
            margin-right: 5px;
          }
        }
        span{
          display: flex;
          align-items: center;
          color: #333;
        }
      }
    }
    .btn{
      width: 250px;
      height: 44px;
      line-height: 44px;
      background: #333;
      color: #fff;
      text-align: center;
      margin: 75px auto 30px;
      font-size: 16px;
    }
    .foo{
      font-size: 12px;
      color: #999;
      width: 300px;
      margin: auto;
      line-height: 22px;
    }
  }
</style>
<style lang="less">
  body{
    background: #ecf0f4!important;
    padding: 0!important;
  }
</style>



